
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0;
}
table{border-collapse:collapse;border-spacing:0;
}
fieldset,img{border:0;
}
address,caption,cite,code,dfn,th,var,i,b{font-style:normal;font-weight:normal;
}
ol,ul{list-style:none;
}
caption,th{text-align:left;
}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;
}
q:before,q:after{content: '';
}
abbr,acronym{border:0;
}
input,select,button {outline:none;
}
* {
  -webkit-touch-callout: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 100%;
}
a {
  text-decoration: none;
}
img {
  pointer-events: none;
}
.slide-fade{
  position: absolute;left:0;right: 0;
}
.slide-fade-enter-active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.slide-fade-leave-active {

  -webkit-transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);

  transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
{
  left:0;right: 0;
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
  opacity: 0;
}

.goods {
  width: 100%;
  color: #445266;
  background-color: #efeff4;
  position: relative;
}
.goods .header {
  background-color: #ffffff;
  padding-bottom: 2rem;
  margin-bottom: 0.8rem;
}
.header .goodsIntroduce {
  padding: 0 10.93333%;
}
.goodsIntroduce > p:nth-child(1) {
  text-align: center;
  font-size: 1.06rem;
  font-weight: bold;
  margin-top: 1.3rem;
  margin-bottom: 1.125rem;
}
.goodsIntroduce > p:nth-child(2) {
  font-size: 0.935rem;
}
.goodsDetails {
  background-color: #ffffff;
  padding: 0 10.93333%;
  padding-bottom: 3rem;
}
.goodsDetails > p:nth-child(1) {
  text-align: center;
  font-size: 1.3rem;
  font-weight: bold;
  padding-top: 1.03rem;
}
.goodsDetails > p:nth-child(2) {
  margin: 1.4rem 0;
}
.goodsDetails .photoShow {
  color: #bec1c8;
}
.goodsDetails .photoShow > img {
  width: 100%;
}
.goodsDetails .photoShow > p {
  text-align: center;
  padding-top: 0.8rem;
  padding-bottom: 2rem;
}
.footer {
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 3px 0;
}
.footer > img {
  width: 1rem;
}
.footer .exchangeNum {
  font-weight: bold;
  margin-left: 0.5rem;
}
.footer .exchange {
  color: #ffffff;
  padding: 0.9rem 2.18rem;
  font-size: 0.875rem;
  background-color: #4dc7e7;
  border-radius: 5px;
  margin-left: 1rem;
  margin-right: 0.3rem;
}

.swiper-container {
  width: 100%;
  height: 21.18rem;
}
.swiper-wrapper {
    width: 100%;
    height: 100%;
}
.swiper-slide {
    background-position: center;
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
    width: 0.5rem;
    height: 0.5rem;
    display: inline-block;
    background: rgba(255, 255, 255, 0.5);
}

